* {
  font-family: 'Roboto', sans-serif; }

header {
  position: relative;
  height: 50px; }

nav {
  position: fixed;
  width: 100%;
  height: 50px;
  background-color: #212121;
  z-index: 2; }

.menu {
  display: flex;
  justify-content: space-between;
  padding: 13px 70px;
  margin: 0;
  list-style: none;

  .menu_link {
    font-size: 15px;
    font-weight: 300;
    font-style: normal;
    color: #ffffff; }

  .menu_item {
    border-right: 2px dotted black;
    padding-right: 28px; } }

.subheader {
  position: absolute;
  top: 50px;
  width: 100%;
  height: 84px;
  background-color: rgba(0, 0, 0, 0.6);

  .subheader_logo {
    display: block;
    max-width: 100%;
    margin-top: 16px; }

  .subheader_logotext {
    margin-top: 6px;
    font-size: 13px;
    font-weight: 100;
    color: rgba(255, 255, 255, 0.5); }

  .subheader_call {
    margin-top: 19px;
    font-size: 12px;
    font-weight: 300;
    color: #ffffff;
    text-align: right; }

  .subheader_phone {
    display: block;
    margin-top: 8px;
    font-size: 22px;
    font-weight: 400;
    color: #ffffff;
    text-align: right;
    line-height: 12px; }

  .subheader_btn {
    margin-top: 19px;
    width: 172px;
    height: 46px;
    box-shadow: inset 0px -3px 0 0 rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    background-image: linear-gradient(to top, #1eacc7, #4ce2ff);
    text-shadow: 0.7px 0.7px 0 rgba(0, 0, 0, 0.21);
    font-size: 14px;
    font-weight: normal;
    text-align: center;
    color: #ffffff;
    text-transform: uppercase; } }

.promo {
  min-height: 800px;
  background: url('../img/car.jpg') center (center / cover) no-repeat;
  padding: 150px 0 135px 0;

  .promo_header {
    font-size: 60px;
    font-weight: 700;
    text-align: center;
    color: #ffffff;
    line-height: 60px;
    margin: 0; }

  .promo_subheader {
    margin-top: 22px;
    font-size: 36px;
    font-weight: bold;
    text-align: center;
    color: #ffffff;
    line-height: 36px; }

  .promo_descr {
    margin-top: 23px;
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
    text-align: center;
    color: #ffffff; }

  .promo_btn {
    display: block;
    margin: 118px auto 0 auto;
    width: 259px;
    height: 63px;
    box-shadow: inset 0px -4px 0 0 rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    background-image: linear-gradient(to top, #1eacc7, #4ce2ff);
    font-size: 18px;
    font-weight: normal;
    text-align: center;
    color: #ffffff; } }

.reasons {
  min-height: 900px;
  padding: 71px 0 65px 0; }

.label {
  display: block;
  margin: 0 auto;
  width: 112px;
  height: 24px;
  background-color: #000000;
  border: none;
  font-size: 11px;
  font-weight: 100;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase; }

.reasons {
  .reasons_header {
    margin-top: 38px;
    font-size: 38px;
    font-weight: bold;
    text-align: center;
    color: #222222;
    text-transform: uppercase; }

  .reasons_block {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 35px;

    .reasons_descr {
      width: 343px; } }

  .round {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 26px;
    width: 113px;
    height: 113px;
    border-radius: 100%;
    background-color: #1eacc7; }

  .reasons_descr {
    .reasons_text {
      margin-top: 0;
      width: 341px;
      font-size: 15px;
      font-weight: 300;
      line-height: 21px;
      color: #202020;
      text-align: justify; }

    .reasons_title {
      font-size: 17px;
      font-weight: normal;
      color: #1eacc7; } } }
